.app{
  display: flex;
  flex-direction: column;
  align-items: center;
  margin-top: 64px;
  color: #fff;
}
.coin-symbol{
  text-transform: uppercase;
}
.coin-price{
  width: 130px;
}
.coin-volume{
  width: 190px;
}
.coin-percent{
  width: 80px;
}
.coin-input{
  padding-left: 16px;
  height: 50px;
  width: 300px;
  border: none;
  border-radius: 4px;
  color: #e2e2e2;
  font-size: 20px;
  background-image: linear-gradient(
    -225deg,
    #ac32e4 0%,
    #7918f2 48%,
    #4801ff 100%
    );
  }
  .coin-input::placeholder{
    font-size: 20px;
    color: #e2e2e2;
}
.red{
  color: #f00606;
}
.green{
  color: #11d811;
}
